import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'

Vue.config.productionTip = false

// axios全局配置
axios.defaults.baseURL = 'https://api.bmob.cn/1/classes/blogMess'
// axios.defaults.headers.common['] = ''
// axios.defaults.headers.post['Content-type'] = '"application/json"'
// axios.defaults.get['Accepts'] ='"application/json"'
Vue.prototype.axios = axios


// 自定义全局过滤器
// Vue.filter('sliceLen',function(value){
//   return value.slice(0,80) + '...'
// })
// 
// 自定义全局指令
Vue.directive('theme',{
  // bind是一个钩子函数 
  bind(el,binding,vnode){
    // value就是指令等号右边的字符串
      if(binding.value == 'wide'){
        el.style.maxWidth = '1226px'
      }else if(binding.value == 'narrow'){
        el.style.maxWidth = '768px'
      }
      if(binding.arg == 'bg'){
        el.style.backgroundColor = '#6677cc';
        el.style.padding = '25px 80px';
        el.style.marginTop = '30px'
      }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
