import Vue from 'vue'
import Router from 'vue-router'
// 一级
import Blog from './components/Blog.vue'
import AddBlog from './components/AddBlog.vue'
import Details from './components/Details.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'blog',
      component: Blog
    },
    {
      path: '/addBlog',
      name: 'addBlog',
      component: AddBlog
    },
    {
      path: '*',
      redirect: '/'
    },
    // 获取id
    {
      path:'/amendBlog/:id',
      name:'amend',
      component: AddBlog
    },
    {
      path:'/detailsBlog/:ids',
      name:'details',
      component: Details
    }
  ]
})
