<template>
     <div class="wrapper">
       <div class="single-blog mt-5">
           <div class="blog m-3">
               <h4>{{ blog.title }}</h4> <span>作者: {{ blog.author }}</span>
               <p class="content">{{ blog.content }}</p>
           </div>
       </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            blog: {},
            // 获取路由传过来的id
            id: this.$route.params.ids
        }
    },
    created(){
        this.axios.get('/' + this.id,{
            headers: {
                "X-Bmob-Application-Id": 'af9a50625f6548a369dae190228ac2e2',
                "X-Bmob-REST-API-Key": 'ca28abeb0cdd68780bbc6c16a4a3730c',
            }
        }).then(data => {
            this.blog = data.data
        })
    }
}
</script>

