<template>
    <div class="wrapper container"  v-theme:bg='"narrow"'>
       <input type="text" class="search mt-5" placeholder="搜索" v-model="search" >
       <div class="single-blog">
           <!-- 这里循环的数组并不是blogs 而是过滤后的数组 -->
           <div class="blog mt-3" v-for="(blog,index) in filterBlog" :key="index">

               <h4 v-rainbow>{{ blog.title }}</h4> <span>作者: {{ blog.author | toUppercase}}</span>
               <!-- 后面的sliceLen是一个自定义过滤器 在main.js中定义(内容截取长度) -->
               <p class="content">{{ blog.content | sliceLen }}</p>
               <router-link tag="button" :to="'/detailsBlog/' + blog.objectId" class="btn one">查看详情</router-link>
               <router-link tag="button" :to="'/amendBlog/' + blog.objectId" class="btn two">修改</router-link>
               <button @click="deleteBlog(blog.objectId)" class="btn three">删除</button>
           </div>
       </div>
    </div>
</template>
<script>
import { filter } from 'minimatch';
export default {
    data(){
        return {
            blogs: [],
            search: ''
        }
    },
    methods: {
        deleteBlog: function(id){
              this.axios.delete('/'+id,{
                headers: {
                    "X-Bmob-Application-Id": 'af9a50625f6548a369dae190228ac2e2',
                    "X-Bmob-REST-API-Key": 'ca28abeb0cdd68780bbc6c16a4a3730c',
                }
            }).then(data=>{
                const reg = /Mess\/((\w|\d)+)/;
                const url = data.config.url;
                const id = url.match(reg)[1];
                this.blogs.forEach(ele => {
                    // 找寻数据里面删除的值然后将数据的值删掉比页面刷新更高效
                    for(let item in ele){
                        if(ele[item] == id){
                            const index = this.blogs.indexOf(ele)
                            this.blogs.splice(index,1)
                        }
                    }
                })
                // this.$router.push({name:'blog'})
            },err => {
                alert(err.response.data.error)
            })
        }
    },
    // 定义一个过滤器 用来过滤搜索框中的关键字出来 可以利用title值
    computed: {
        filterBlog: function(){
            // 这里return的是一个boolean值 如果为真就显示关键字所关联的内容 为假就显示全部
            // filter参数是一个回调函数
            return this.blogs.filter(blog =>{
                 return blog.title.match(this.search)
            })
        }
    },
    created(){
        this.axios.get('',{
            headers: {
                "X-Bmob-Application-Id": 'af9a50625f6548a369dae190228ac2e2',
                "X-Bmob-REST-API-Key": 'ca28abeb0cdd68780bbc6c16a4a3730c',
            }
        }).then(data =>{
            this.blogs = data.data.results;
            // 可以通过过滤器实现
            // this.blogs.forEach(ele =>{
            //    ele.content = ele.content.slice(0,100) + '...'
            // //    console.log(ele.objectId)
            // })
        })
    },
    // 自定义局部过滤器
    filters: {
        // 内容长度截取
        sliceLen(value){
            return value.slice(0,80) + '...'
        },
        // 作者姓名大写
        toUppercase(value){
            return value.toUpperCase()
        }
    },
    // 自定义局部指令
    directives: {
        rainbow: {
            bind(el,binding,vnode){
                // 随机一个数 转换为十六进制 再取其中六位作为颜色 因为是小数 前面0.两位不要 从第三位开始截取
                el.style.color = '#' + Math.random().toString(16).slice(2,8)
            }
        }
    }
}
</script>
<style scoped>
    .search{
        width: 100%;
        margin-top: 30px!important
    }
    h4{
        display: inline-block;
        margin-right: 20px
    }
    span{
        font-weight: 600
    }
    .blog{
        position: relative;
        background: #ccc;
        padding: 20px 20px 70px
    }
    .content{
        margin-top: 20px;
        text-indent: 0.8em 
    }
    .btn{
        position: absolute;
        bottom: 15px
    }
    .one{
        right: 160px
    }
    .two{
        right: 90px;
    }
    .three{
        right: 20px
    }
</style>


