<template>
    <div class="wrapper mt-3 container">
        <h3>写博客</h3>
        <form id="form" action="POST" class="mt-4" v-if="show" >
            <label for="">
                <p>主题:</p>
                <input class="text" type="text" required v-model="blog.title">
            </label>
            <label for="">
                <p>内容:</p>
                <textarea class="textarea" type="textarea" required v-model=blog.content></textarea>
            </label>
            <label for="">
                <p>分类:</p>
                <span>Vue.js </span> <input type="checkbox" value="Vue.js" v-model="blog.belong">
                <span>Angular4 </span> <input type="checkbox" value="Angular4" v-model="blog.belong">
                <span>Node.js </span> <input type="checkbox" value="Node.js" v-model="blog.belong">
                <span>React.js </span> <input type="checkbox" value='React.js' v-model="blog.belong">
            </label>
            <select class="text author" v-model='blog.author'>
                <p>作者:</p>
                <option :key="index" v-for="(autho,index) in authors">{{ autho }}</option>
            </select>
            <button @click.prevent="subPost" class="btn sucess mt-3">{{text}}</button>
        </form>
        <h5 v-if="!show" class="mt-3">博客{{tt}}成功,点击博客预览!</h5>
    </div>
</template>
<style scoped>   
    .wrapper{
        max-width: 60%;
    }
    h3{
        font-weight: 600
    }
    #form{
        width: 100%;
    }
    #form label{
        display: block;
    }
    #form .text{
        width: 100%
    }
    #form .textarea{
        width: 100%;
        height: 26vh;
    }
    p{
        margin-bottom: 10px;
        font-weight: 700
    }
    #form span{
        display: inline-block;
        margin: -10px 5px 0 15px;
        font-weight: 500
    }
    .author{
        border: 1px solid #999;
        border-radius: 10px;
        /* 去除input边框线 */
        outline: none;
        text-indent: 0.4em;
        padding: 3px 0
    }
    .sucess{
        background: rgb(236, 58, 58);
        color: #fff
    }
</style>
<script>
export default {
    data(){
        return {
            blog:{
                title: '',
                content: '',
                belong: [],
                author: ''
            },     
            authors: [ 'xiaoming', 'zhangsan', 'lisi' ],
            show: true,
            // 获取router.js中的id
            id: this.$route.params.id,
            text: '添加博客',
            tt: '添加'
        }
    },
    methods: {
        subPost: function(){
            if((this.judeg())){
                if(this.text == '添加博客'){
                this.axios.post('',this.blog,{
                    headers: {
                        "X-Bmob-Application-Id": 'af9a50625f6548a369dae190228ac2e2',
                        "X-Bmob-REST-API-Key": 'ca28abeb0cdd68780bbc6c16a4a3730c',
                        "Content-Type": "application/json"
                    }
                }).then(data =>{
                    this.show = false;
                    console.log('add')
                })
                }else if(this.text == '重新上传'){
                    this.axios.post('/'+ this.id,this.blog,{
                        headers: {
                            "X-Bmob-Application-Id": 'af9a50625f6548a369dae190228ac2e2',
                            "X-Bmob-REST-API-Key": 'ca28abeb0cdd68780bbc6c16a4a3730c',
                            "Content-Type": "application/json"
                        }
                    }).then(data =>{
                        this.show = false;
                        this.tt = '重新'
                    },err =>{
                        console.log(err.response)
                    })
                }
            }
        },
        judeg: function(){
            try{
                if(!this.blog.title){
                    throw '请标明主题!'
                }
                if(!this.blog.content){
                    throw '内容不能为空!'
                }
                if(!this.blog.belong.length){
                    throw '请选择所属分类!'
                }
                if(!this.blog.author){
                    throw '请选择此博客作者!'
                }
            }catch(e){
                alert(e)
                return false
            }
            return true
        }
    },
    created(){
        if(this.id){
            this.axios.get(`/${this.id}`,{
                headers: {
                    "X-Bmob-Application-Id": 'af9a50625f6548a369dae190228ac2e2',
                    "X-Bmob-REST-API-Key": 'ca28abeb0cdd68780bbc6c16a4a3730c',
                }
            }).then(data =>{
                this.blog = data.data;
                this.text = '重新上传'
            })
        }
    }
}
</script>


